.App {
  text-align: center;
  background-color:#ffffff;
  
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
  
  
}

nav li {
  display: inline-block;
  margin-right: 40px; /* Adjust the value to increase or decrease space */
margin-left: 20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}
.first-letter {
  font-size: 90px;
  line-height: 70px;
  color: red;
}
.mid-letter {
  font-size: 90px;
  line-height: 70px;
  color: red;
}
.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
